
body,html{margin:0;padding:0;font-family:sans-serif;color:#555
}
body{font-size:16px
}
h2,h3,h4{color:#35495E
}
h2{margin:2em 0 1.5em 0
}
h3{margin:0 0 1em 0
}
h4{margin-bottom:0.5em
}
section{width:80%;max-width:1000px;margin:0 auto 2em auto
}
a{color:#41B883;text-decoration:none
}
header{background:#19232d;color:#fff;text-align:center;height:150px;display:-ms-flexbox;display:flex;-ms-flex-flow:column nowrap;flex-flow:column nowrap;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;position:relative
}
header h1{margin:0;color:#51c28f
}
header h1 .version{font-weight:300;margin:0 .2em 0 .1em
}
header p{margin-bottom:0;color:rgba(255,255,255,0.7);font-size:300
}
header p .version{font-weight:100;background-color:rgba(53,73,94,0.3);padding:.1em .5em;border-radius:3px
}
nav.top-nav{background:#19232d;position:relative
}
nav.top-nav .title{display:none
}
nav.top-nav .v1x{display:inline-block;color:rgba(255,255,255,0.5);position:absolute;z-index:301;right:1em;top:0.9em;font-size:0.9em
}
nav.top-nav .v1x:after{content:'This way to the';position:absolute;font-size:.8em;text-transform:uppercase;top:-1.6em;left:0;opacity:0.5
}
nav.top-nav .v1x a{color:rgba(255,255,255,0.6);transition:color .3s
}
nav.top-nav .v1x a:hover{color:rgba(255,255,255,0.8)
}
nav.top-nav.stick{position:fixed;top:0;left:0;right:0;z-index:300
}
nav.top-nav.stick .title{display:inline-block;color:rgba(255,255,255,0.8);position:absolute;z-index:301;left:1em;top:.8em
}
nav.top-nav.stick .v1x:after{content:'';display:none
}
nav.top-nav ul{list-style:none;padding:0;margin:0;display:-ms-flexbox;display:flex;-ms-flex-flow:row nowrap;flex-flow:row nowrap;-ms-flex-pack:center;justify-content:center;-ms-flex-align:stretch;align-items:stretch
}
nav.top-nav ul li{margin:0 1em;overflow:hidden
}
nav.top-nav ul li a{color:rgba(255,255,255,0.5);display:block;padding:.8em .5em;border-radius:5px;transition:color .3s;position:relative;overflow:hidden;z-index:1
}
nav.top-nav ul li a:hover{color:rgba(65,184,131,0.7);cursor:pointer
}
nav.top-nav ul li a.active{color:#51c28f
}
nav.top-nav ul li a.active:after{content:'';display:block;width:0;height:0;border:10px solid transparent;border-bottom-color:#fff;position:absolute;bottom:-3px;left:calc(50% - 10px);z-index:3
}
main.content.nav-affixed{padding-top:44px
}
.fade{transition:opacity .5s ease
}
.fade-enter-active,.fade-leave-active{opacity:0
}
.description p{line-height:1.5em
}
.description code{color:#35495E;background:rgba(0,0,0,0.03);border-radius:3px;padding:0.2em 0.5em;margin:0 0.2em;vertical-align:baseline
}
.preview{padding:2em 1.5em;border:1px solid rgba(0,0,0,0.07);border-radius:3px
}
.preview b{font-size:0.9em;color:#777
}
.preview p{margin:0;padding:.5em 0 1.5em 0
}
.preview p:last-of-type{padding-bottom:0
}
.codes pre{position:relative;font-size:0.8em
}
.codes pre code{padding:2em;border-radius:3px
}
.codes pre:after{content:attr(data-title);position:absolute;top:0.3em;right:0.8em;font-size:0.8em;color:rgba(53,73,94,0.4)
}
#mostlyUsedSamples .section-title{padding-left:20%
}
#mostlyUsedSamples .block{padding:5em 0 0 20%
}
#mostlyUsedSamples .block:first-of-type{padding-top:0
}
#mostlyUsedSamples .block .title{position:relative;margin-bottom:1.5em
}
#mostlyUsedSamples .block .title a.anchor{position:absolute;left:-1em
}
#mostlyUsedSamples .block .description,#mostlyUsedSamples .block .preview,#mostlyUsedSamples .block .codes{margin-left:1.5em
}
#mostlyUsedSamples .block.footer-links a{cursor:pointer
}
.subtitle{margin:0;padding:1em 0 0.5em 0;position:relative
}
.subtitle a.anchor{position:absolute;left:-1em
}
.capitailized{text-transform:uppercase
}
.group-label{font-weight:bold;display:block;padding:0.5em 0 0.3em 1em
}
.config-row{padding:0.5em 0
}
.config-row .row-label{display:inline-block;font-weight:bold;width:5em;text-align:right
}
.config-row .options{min-width:3.5em;display:inline-block
}
.config-row.group{padding-left:1em
}
.config-block:first-of-type .subtitle{padding-top:0
}
aside.previews{position:fixed;width:320px;left:10%;top:0;z-index:10;padding-top:220px
}
#playground main{padding-left:420px
}
#playground main .codes pre,#playground main .codes code{overflow-x:auto
}
#playgroundPreview{box-sizing:border-box;background:#fff
}



